<template>
	<view class="l-module">
		<image  class="l-module-img" v-if="isLoad" @load="getImgData" @error="getImgDataError" :src="dataInfo.icon" mode="widthFix"></image>
		<view class="l-module-title">
			{{ dataInfo.title || '' }}
		</view>
	</view>
</template>

<script>
	export default {
		props:{
			dataInfo: {
				type: Object,
				default(){
					return {}
				}
			},
			index: Number
		},
		data(){
			return {
				isLoad: true,
				number: 0
			}
		},
		methods:{
			getImgDataError(e){
				console.log(e)
				this.getImgData()
			},
			getImgData(e) {
				
				let _this = this;
				try{
					let index = _this.index
					let createSelectorQuery = uni.createSelectorQuery().in(_this);
					createSelectorQuery.select('.l-module').fields({
						size: true
					}, res => {
						let {
							height, width
						} = res;
						if(width && height){
							_this.$emit('renderBox', {
								index,
								height,
								width
							})
						}else{
							_this.anewLoadImg()
						}
					}).exec()
				}catch(e){
					_this.anewLoadImg()
					throw new Error(e);
				}
				
			},
			// 渲染超出6s就停止继续渲染
			anewLoadImg(){
				++this.number;
				if(this.number > 100){
					return
				}
				this.isLoad = false;
				setTimeout(() => {
					this.isLoad = true;
				}, 60)
			}
		}
	}
</script>

<style scoped lang="scss">
	.l-module{
		position: absolute;
		z-index: 6;
		width: 100%;
		border-radius: 12rpx;
		color: #3C3C43;
		overflow: hidden;
		font-size: 22rpx;
		background-color: #F8FBFF;
		
		&-img{
			width: 100%;
			max-height: 500rpx;
			background-color: #F1F1F1;
		}
		
		&-title{
			padding: 14rpx 10rpx 6rpx;
			font-size: 24rpx;
			line-height: 34rpx;
			word-break:break-all;
			display: -webkit-box;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: 2;
			overflow: hidden;
			max-height: 64rpx;
		}
	}
</style>
